<template>
  <mdb-carousel
    :interval="8000"
    slide
    :items="items"
    indicators
    id="carousel"
    class="img-fluid"
  ></mdb-carousel>
</template>
<script>
  import { mdbCarousel } from "mdbvue";
  export default {
    name: "CarouselPage",
    components: {
      mdbCarousel
    },
    data() {
      return {
        items: [
          {
            img: true,
            src:
              require ("../assets/Team/Grupal/team1.jpeg")
          },
          {
            img: true,
            src:
               require ("../assets/Team/Grupal/team2.jpeg")
          },
          {
            img: true,
            src:
              require ("../assets/Team/Grupal/team3.jpeg")            
          }
        ]
      };
    }
  };
</script>
